/**
    
 */
 *,
 ::before,
 ::after {
   box-sizing: border-box;
 }
 
 /**
        ,
       class
  */
 :where(ul, ol):where([class]) {
   padding-left: 0;
 }
 
 /**
      body    ,
       class
  */
 body,
 :where(blockquote, figure):where([class]) {
   margin: 0;
 }
 
 /**
        ,
       class
  */
 :where(
   h1,
   h2,
   h3,
   h4,
   h5,
   h6,
   p,
   ul,
   ol,
   dl
 ):where([class]) {
   margin-block: 0;
 }
 
 :where(dd[class]) {
   margin-left: 0;
 }
 
 :where(fieldset[class]) {
   margin-left: 0;
   padding: 0;
   border: none;
 }
 
 /**
       ,
       class
  */
 :where(ul[class]) {
   list-style: none;
 }
 
 :where(address[class]) {
   font-style: normal;
 }
 
 /**
       ,
         ,
         
  */
 p {
   --paragraphMarginBottom: 24px;
 
   margin-block: 0;
 }
 
 /**
          class,
          
  */
 p:where(:not([class]):not(:last-child)) {
   margin-bottom: var(--paragraphMarginBottom);
 }
 
 
 /**
        
  */
 img,
 video {
   display: block;
   max-width: 100%;
   height: auto;
 }
 
 /**
        
  */
 input,
 textarea,
 select,
 button {
   font: inherit;
 }
 
 html {
   /**
        
     (, ,   ""    )
    */
   height: 100%;
   /**
         
       /  
    */
   scrollbar-gutter: stable;
 }
 
 /**
    
  */
 html,
 :has(:target) {
   scroll-behavior: smooth;
 }
 
 body {
   /**
        
     (, ,   ""    )
    */
   min-height: 100%;
   /**
      
    */
   line-height: 1.5;
 }
 
 /**
           DevTools
  */
 a:where([class]) {
   display: inline-flex;
 }
 
 /**
   -    
  */
 button,
 label {
   cursor: pointer;
 }
 
 /**
       svg-
   (  ,    
    fill   'none'    'url')
  */
 :where([fill]:not(
   [fill="none"],
   [fill^="url"]
 )) {
   fill: currentColor;
 }
 
 /**
       svg-
   (  ,    
    stroke   'none')
  */
 :where([stroke]:not(
   [stroke="none"],
   [stroke^="url"]
 )) {
   stroke: currentColor;
 }
 
 /**
           svg-
  */
 svg * {
   transition-property: fill, stroke;
 }
 
 /**
        'collapse' 
  */
 :where(table) {
   border-collapse: collapse;
   border-color: currentColor;
 }
 
 /**
         ,
       
  */
 @media (prefers-reduced-motion: reduce) {
   *,
   ::before,
   ::after {
     animation-duration: 0.01ms !important;
     animation-iteration-count: 1 !important;
     transition-duration: 0.01ms !important;
     scroll-behavior: auto !important;
   }
 }